.App {
  text-align: center;
}

.maindiv {
  display: flex;
  flex-direction: column ;
  background-color: black;
  align-content: center;
  align-items: center;
  /* justify-content: center; */
  height: 720px;

}
.column {
  display: flex; 
  flex-direction: row;
  background-color: black;
}
.row {
  width: 50px;
  height: 50px;
  background-color: darkgrey;
  margin: 5px;
}
.row:hover  { 
  background-color: blue ;
}
.titleText {
  font-size: 50px;
  color: white;
  margin-bottom: 50px;
  text-align: center;
}
.letters {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
  color: white;
}
.info {
  position: absolute;
  width: 80px;
  height: 80px;
  background-color: red;
  align-self: flex-end;
  border-radius: 90px;
  text-align: center;
  cursor: pointer;
}
.restartbutton {
  position: absolute;
  width: 80px;
  height: 80px;
  background-color: red;
  align-self: flex-start;
  border-radius: 90px;
  text-align: center;
  cursor: pointer;
}
.iconlayer {
  align-self: center;
  align-items: center;
  /* background-color: white; */
  justify-content: center;
  justify-items: center;
  justify-self: center;
  padding-top: 12px;
}